.popup-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  overflow: auto;
  text-align: center;
  visibility: hidden;
  background-color: rgba(102, 153, 204, 0.8);
  opacity: 0;
  -webkit-transition: all 0.6s;
  -o-transition: all 0.6s;
  transition: all 0.6s;
}

.popup-wrapper::after {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  content: "";
}

.popup-open {
  visibility: visible;
  opacity: 1;
}

.popup {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
