  border-radius: 6px;
  outline: medium none;
  -webkit-box-shadow: 3px 3px 15px #bbb;
          box-shadow: 3px 3px 15px #bbb;
}

.popup__close {
  position: absolute;
  top: 8px;
  right: 8px;
  z-index: 10;
  display: block;
  width: 6px;
  height: 17px;
  padding: 1px 9px 4px 9px;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  background-image: none;
  border: none;
}

.popup__close::before,
.popup__close::after {
  position: absolute;
  top: 50%;
  right: 4px;
  display: inline-block;
  width: 15px;
  height: 2px;
  margin-top: -1px;
  content: "";
  background-color: #fff;
  -webkit-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
  -webkit-transition: width 0.3s, right 0.3s, -webkit-transform 0.3s;
  transition: width 0.3s, right 0.3s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, width 0.3s, right 0.3s;
  transition: transform 0.3s, width 0.3s, right 0.3s;
  transition: transform 0.3s, width 0.3s, right 0.3s, -webkit-transform 0.3s;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.popup__close::before {
  -webkit-transform: rotate(225deg);
      -ms-transform: rotate(225deg);
          transform: rotate(225deg);
}

.popup__close::after {
  -webkit-transform: rotate(135deg);
      -ms-transform: rotate(135deg);
          transform: rotate(135deg);
}

.popup__title {
  font-size: 30px;
  color: #fff;
}

.popup__description {
  font-size: 18px;
  color: #fff;
}

