.form .bar::after {
  right: 50%;
}

.form .bar::before,
.form .bar::after {
  position: absolute;
  bottom: 1px;
  width: 0;
  height: 2px;
  content: "";
  background: #ff44d9;
  -webkit-transition: 0.2s ease all;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
}

.form input:focus ~ .bar::before,
.form input:focus ~ .bar::after {
  width: 50%;
}

