.button {
  display: inline-block;
  padding: 15px 35px;
  margin: 0 0 25px;
  font-weight: 600;
  color: #348efe;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid #9dd1ff;
  border-radius: 10px;
  -webkit-transition: all 150ms ease-in-out;
  -o-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}

.button:hover,
.button:focus {
  cursor: pointer;
  background-color: #e1f1ff;
}

.button.is-featured {
  color: #fff;
  background: -webkit-gradient(linear, left top, right top, from(#339), to(#f0c));
  background: -o-linear-gradient(left, #339, #f0c);
  background: linear-gradient(to right, #339, #f0c);
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}

.button.is-featured:hover,
.button.is-featured:active {
  background: -webkit-gradient(linear, left top, right top, from(#f0c), to(#339));
